import Vue from 'vue'
import Vuex from "vuex"
import axios from "axios"

Vue.use(Vuex);
export const store = new Vuex.Store({
    state: {
        shopdetails: [],
        item: [],
        token: null,
        shopdata: {
            shopcart: false,
            isshop: true,
            list: [],
            listflag: []
        }
    },
    mutations: {
        // token
        gettoken(state, token) {
            state.token = token
        },
        removetoken(state) {
            state.token = null
            sessionStorage.removeItem("id")
        },

        get(state, item) {
            state.shopdetails = item
        },
        getitem(state, item) {
            state.item = item
        },
        add(state, item) {
            state.shopdata.isshop = false;
            let num = 0


            state.shopdata.list.map((value, index) => {
                if (value.id == item.id && value.color == item.color && value.Memory == item.Memory) {
                    state.shopdata.shopcart = true
                    num = index
                } else {
                    state.shopdata.shopcart = false
                }
            })

            if (state.shopdata.shopcart) {
                state.shopdata.list[num].count++;
                sessionStorage.setItem("setlist", JSON.stringify(state.shopdata));
            } else {
                state.shopdata.list.push(item)
                state.shopdata.listflag.push({
                    id: item.id,
                    cid:item.cid,
                    flag: false
                })
                sessionStorage.setItem("setlist", JSON.stringify(state.shopdata));
            }


        },
        increase(state, item) {
            let index = state.shopdata.list.indexOf(item)
            if (state.shopdata.list[index].count < state.shopdata.list[index].stock) {
                state.shopdata.list[index].count++;
            }
            sessionStorage.setItem("setlist", JSON.stringify(state.shopdata));
        },
        decrease(state, item) {
            let index = state.shopdata.list.indexOf(item)
            if (state.shopdata.list[index].count > 0) {
                state.shopdata.list[index].count--;
            }
            sessionStorage.setItem("setlist", JSON.stringify(state.shopdata));
        },
        del(state, item) {
            let index = state.shopdata.list.indexOf(item)
            state.shopdata.list[index].count = 0
            state.shopdata.list.splice(index, 1)
            if (state.shopdata.list.length === 0) {
                state.shopdata.isshop = true;
            }
            sessionStorage.setItem("setlist", JSON.stringify(state.shopdata));
        },
        setlist(state) {
            state.shopdata = JSON.parse(sessionStorage.getItem("setlist"))
        }
    },
    actions: {
        getlist({
            commit
        }) {
            const obj1 = {
                method: "GET",
                url: `./assets/data/homeGoods.json`
            };

            const obj2 = {
                method: "GET",
                url: `./assets/data/homeGoods.json`
            };
            axios(obj1).then(item => {
                commit("get", item.data)
            })
            axios(obj2).then(item => {
                commit("getitem", item.data)
            })
        }
    },
    getters: {
        total(state) {
            const obj = {
                count: 0,
                price: 0
            }
            state.shopdata.listflag.map(item => {
                state.shopdata.list.map(value => {
                    if (item.flag) {
                        if (item.cid === value.cid) {
                            obj.count += value.count
                            obj.price += value.count * value.price
                        }
                    }
                })

            })
            return obj
        },
        num(state) {
            let num = 0
            state.shopdata.list.map(value => {
                num += value.count
            })
            return num
        }
    }
})