            <p class="option-cover-right-price">￥{{ currentModel.edition_price }}</p>
            <p
              class="option-cover-right-title"
            >{{ cartdetails.name }} {{ currentModel.Memory }} {{ currentColor.color_list }}</p>
          </div>
        </div>
