<script>
import Vuex from "vuex";
import axios from "axios";
export default {
  name: "shopdeta",
  data() {
    return {
      id: "",
      // 商品内容
      cartdetails: "",

      // 当前版本
      currentModel: "",
      // 版本
      Model: "",
      // 当前颜色
      currentColor: "",
      // 颜色
      colorlist: "",
      // 数量
      count: 1,
      // 是否显示购物车
      show: false,
      // 加入购物车
      buyGoodsInfo: []
    };
  },
  computed: {
    ...Vuex.mapState(["shopdata"]),
    ...Vuex.mapGetters(["num"])
  },
  methods: {
    ...Vuex.mapMutations(["add"]),
    showPopup() {
      
      if (sessionStorage.getItem("id")) {
     
        this.show = true;
      }else{
        
        this.$router.push({ path: 'login', query: { redirect: `shopdeta` }})
      }
      
    },
    selected(value) {
      this.currentModel = value;

      this.cartdetails.edition.map(item => {
        if (item.Memory == value.Memory) {
          this.colorlist = item.color;
        }
      });
    },
    // 选择颜色
    selected_color(value) {
      console.log(value);
      this.currentColor = value;
      this.colorlist.map(item => {
        if (item.color_list == this.currentColor.color_list) {
          this.currentColor = item;
        }
      });
    },
    // 加入购物车（id，版本颜色,数量）
    add() {
      var myDate = new Date();
      var cid = myDate.getTime();  
      var buyGoodsInfo = {
        cid,
        id: this.id,
        count: this.count,
        Memory: this.currentModel.Memory,
        color: this.currentColor.color_list,
        price: this.currentModel.edition_price,
        img: this.currentColor.img,
        name: this.cartdetails.name
      };
      this.$store.commit('add',buyGoodsInfo)
      this.show = false;
    },
    increase() {
      this.count++;
    },
    decrease() {
      if (this.count > 1) {
        this.count--;
      }
    }
  },
  created() {
    // console.log(this.list);
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      
      vm.$store.state.item.map(item => {
        if (item.id == vm.$route.query.redirect) {
          vm.id = vm.$route.query.redirect;
          vm.cartdetails = item;
          vm.Model = item.edition;
          vm.currentModel = item.edition[0];
          vm.colorlist = item.edition[0].color;
          vm.currentColor = item.edition[0].color[0];
         
        }
      });
    });
  }
};
</script>

